/**
 *
 * Content
 *
 * @author Takuto Yanagida
 * @version 2021-11-19
 *
 */


window['NACSS'] = window['NACSS'] || {};


(function (NS) {

	(function () {
		/**
		 *
		 * Image Alt
		 *
		 * @author Takuto Yanagida
		 * @version 2021-11-19
		 *
		 */
		
		
		function initialize(ts) {
			for (const t of ts) {
				if (!t.alt && t.src) {
					let src = t.src.trim();
					if (src.endsWith('/')) src = src.substring(0, src.length - 1);
					const idx = src.lastIndexOf('/');
					if (idx === -1) continue;
					t.alt = src.substring(idx + 1);
				}
			}
		}
		
		NS.contentImageAlt = initialize;
	})();

	(function () {
		/**
		 *
		 * Figure Caption
		 *
		 * @author Takuto Yanagida
		 * @version 2021-11-19
		 *
		 */
		
		
		// Wrap figure captions with spans for Firefox
		function initialize(figs) {
			const ua = window.navigator.userAgent.toLowerCase();
			if (ua.includes('firefox')) {
				for (const fig of figs) {
					fig.style.width = '';
		
					const fcs = fig.getElementsByTagName('figcaption');
					const fc  = fcs.length ? fcs[0] : null;
					if (fc) {
						fc.innerHTML = `<span>${fc.innerHTML}</span>`;
					}
				}
			}
		}
		
		NS.contentFigureCaption = initialize;
	})();

	(function () {
		/**
		 *
		 * Iframe Aspect
		 *
		 * @author Takuto Yanagida
		 * @version 2021-11-19
		 *
		 */
		
		
		function initialize(fs) {
			for (const f of fs) {
				if (!f.style.aspectRatio) {
					const [w, h] = extractIframeSize(f);
					if (w && h) {
						f.style.aspectRatio = `${w} / ${h}`;
					}
				}
			}
		}
		
		function extractIframeSize(f) {
			let w = parseInt(f.width);
			let h = parseInt(f.height);
			if (w && h) return [w, h];
		
			const sW = f.style.width;
			const sH = f.style.height;
			if (sW.indexOf('px') !== sW.length - 2) return [0, 0];
			if (sH.indexOf('px') !== sH.length - 2) return [0, 0];
			w = parseInt(sW);
			h = parseInt(sH);
			if (w && h) return [w, h];
			return [0, 0];
		}
		
		NS.contentIframeAspect = initialize;
	})();

	(function () {
		/**
		 *
		 * Underline
		 *
		 * @author Takuto Yanagida
		 * @version 2021-11-19
		 *
		 */
		
		
		function initialize(spans, opts = {}) {
			opts = Object.assign({
				styleList: ':ncUnderline',
			}, opts);
		
			for (const span of spans) {
				setStyle(span, opts);
			}
		}
		
		function setStyle(t, opts) {
			const type = t.style.textDecorationLine;
			if (type === 'underline') {
				const re = new RegExp('text-decoration\\s*:\\s*' + type + '\\s*;?', 'gi');
				if (t.getAttribute('style').match(re)) {
					t.removeAttribute('style');
				} else {
					t.style.textDecorationLine = '';
				}
				setClass(t, opts.styleList);
			}
		}
		
		NS.contentUnderline = initialize;
	})();

	/**
	 *
	 * Style Class Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function hasClass(tar, cls) {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			return tar.dataset[key] !== undefined;
		} else {
			return tar.classList.contains(key);
		}
	}
	
	function setClass(tar, cls, enabled = true, val = '') {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			if (enabled) {
				tar.dataset[key] = val;
			} else {
				delete tar.dataset[key];
			}
		} else {
			const c = [key, val].join('-');
			if (enabled) {
				tar.classList.add(c);
			} else {
				tar.classList.remove(c);
			}
		}
	}
	
	function getSelector(cls) {
		if (cls.startsWith(':')) {
			return `*[data-${cls.substr(1).replace(/([A-Z])/g, c => '-' + c.charAt(0).toLowerCase())}]`;
		} else {
			return `*${cls}`;
		}
	}
	

})(window['NACSS']);
